const blockInHour = 1200

function timeout(ms) {return new Promise(resolve => setTimeout(resolve, ms));}

const GetBallance = async function () {
    console.log('Get Account then get Ballance starting!')
    let tweb = await store.state.tronWeb.instance
    let addr
    return new Promise((resolve) => {
        addr = tweb.defaultAddress.base58
        if (addr != false) {
            if (typeof addr !== 'undefined'){
                store.dispatch('ActionSetAddress', addr),
                resolve();
            }
        }
    })
    .then( () => {
        console.log('GetBallance starting! Address:', addr, '\n object ', tweb),
        tweb.trx.getBalance(addr, (err, balance) => {
            if(err)
                return console.error('something wrong', err);
            console.log('balance =', balance);
            store.dispatch('ActionSetTRXBal', balance)
        })
    })
}

const mathdiv = function (A, B) {
    return (A - A%B)/B}

const startEventListener = async function (tweb) {
    console.log('riddleHashContractSource.abi', riddleHashContractSource.abi, 'riddleHashContract', riddleHashContract)
    // Award watch events 
    tweb.contract(riddleHashContractSource.abi, riddleHashContract).Award().watch((err, element) => {
        if(err) {
            console.log('Error while subscribe Award ', err)
        }
        if(element) {
            let res = element.result
            console.log('Award()', res.BlockNumber + "_" + res.StakeId, res.BlockNumber, res.Address, res.Prediction, res.StakeId, res.ActualTail, 
                tronWeb.fromSun(res.StakeValue), tronWeb.fromSun(res.Won), res.isWon)
            store.dispatch('writeStakeAward', {
                _betKeyStakeId: res.BlockNumber + "_" + res.StakeId,
                _BlockNumber:   parseInt(res.BlockNumber),
                _from:          res.Address,
                _Prediction:    parseInt(res.Prediction).toString(16).toUpperCase(),
                _StakeId:       res.StakeId,
                _ActualTail:    parseInt(res.ActualTail).toString(16).toUpperCase(),
                _StakeValue:    parseInt(res.StakeValue),
                _Won:           parseInt(res.Won),
                _isWon:         res.isWon
            })
            console.log('Award() end')
        }
    });
    // Award get history events
    tweb.getEventResult(riddleHashContract, { 
        eventName: 'Award', size: 250, page: 2
    }).then(result => {
        result.reverse().forEach(element => {
            let res = element.result
            console.log('Award read', res.BlockNumber + "_" + res.StakeId, res.BlockNumber, res.Address, res.Prediction, res.StakeId, res.ActualTail, tronWeb.fromSun(res.StakeValue), tronWeb.fromSun(res.Won), res.isWon)
            store.dispatch('writeAllAwards', {
                _betKeyStakeId: res.BlockNumber + "_" + res.StakeId,
                _BlockNumber:   parseInt(res.BlockNumber),
                _from:          res.Address,
                _Prediction:    parseInt(res.Prediction).toString(16).toUpperCase(),
                _StakeId:       res.StakeId,
                _ActualTail:    parseInt(res.ActualTail).toString(16).toUpperCase(),
                _StakeValue:    parseInt(res.StakeValue),
                _Won:           parseInt(res.Won),
                _isWon:         res.isWon
            })
        })
    })
    // Stake watch events 
    tweb.contract(riddleHashContractSource.abi, riddleHashContract).staked().watch((err, element) => {
        if(err) {console.log('Error while subscribe Award ', err)}
        if(element) {
            let res = element.result
            console.log('Award()', res.BlockNumber + "_" + res.StakeId, res.BlockNumber, res.Address, res.Prediction, res.StakeId, tronWeb.fromSun(res.StakeValue))
            store.dispatch('writeAllAwards', {
                _betKeyStakeId: res.BlockNumber + "_" + res.StakeId,
                _BlockNumber:   parseInt(res.BlockNumber),
                _from:          res.Address,
                _Prediction:    parseInt(res.Prediction).toString(16).toUpperCase(),
                _StakeId:       res.StakeId,
                _ActualTail:    '?',
                _StakeValue:    parseInt(res.StakeValue),
                _Won:           0,
                _isWon:         null
            })
        }
    });
}

const startBlockListener = async function (tweb) {
    // get all tick's history
    let isTrue = true

    while (isTrue) {
        await timeout(1000);

        var options = {
            method: 'GET',
            url: 'https://api.shasta.trongrid.io/v1/blocks/latest/events',
            qs: {only_confirmed: 'true'}
        };
    
        await request(options, async function (error, response, body) {
            if (error) {
                throw new Error(error)
                isTrue = false;
            };            
            let block = await tronWeb.trx.getBlockByNumber(JSON.parse(body).data[0].block_number);
            let blocktailhex1 = block.blockID.substring(63)
            //console.log(JSON.parse(body).data[0].block_number, block.blockID, blocktailhex1, parseInt('0x'+blocktailhex1, 16));
            store.dispatch(
                'ActionSetBlockHistory', 
                {
                    "Number": JSON.parse(body).data[0].block_number,
                    "Hash": block.blockID.toUpperCase(),
                    "HashShort": block.blockID.toUpperCase().substring(10, 17) + '...' + block.blockID.toUpperCase().substring(63),
                    "TailHex": blocktailhex1, 
                    "TailDec": parseInt('0x'+blocktailhex1, 16)
                }
            );
        });
    }

    console.log('startEventListener tweb=', tweb)
}

const GetTronWeb = new Promise(function (resolve) {
    store.dispatch('writeRiddleHashContractAddress', riddleHashContract);
    console.log('Connection starting...'/*, 'window.tronWeb=', window.tronWeb, ' window.tronWeb.ready=', window.tronWeb.ready*/, riddleHashContract)
    const tronWebState = {
        installed: !!window.tronWeb,
        loggedIn: window.tronWeb && window.tronWeb.ready
    };

    if(tronWebState.loggedIn) {
        console.log('tronWebState.installed, returning window.tronWeb, window.tronWeb=', window.tronWeb)
        startBlockListener(window.tronWeb)
        startEventListener(window.tronWeb)
        resolve(window.tronWeb);
    } else {
        console.log('tronWebState not installed, tronWebState.installed = ', tronWebState.installed)

        let tries = 0;

        const timer = setInterval( () => {
            console.log('start timer try=', tries , ' tronWebState.installed=', tronWebState.installed)
            if(tries >= 10) {
                console.log('tronWebState not installed, connecting to node, try =', tries)

                window.tronWeb = _tronWeb

                clearInterval(timer);
                tronWebState.installed = !!window.tronWeb;
                tronWebState.loggedIn = true // window.tronWeb && window.tronWeb.ready;
                console.log('tronWebState.installed = ', tronWebState.installed , ' window.tronWeb=', window.tronWeb, ' window.tronWeb.ready=', window.tronWeb.ready)
                startBlockListener(window.tronWeb)
                startEventListener(window.tronWeb)
                resolve(window.tronWeb);
            }

            tronWebState.installed = !!window.tronWeb;
            tronWebState.loggedIn = window.tronWeb && window.tronWeb.ready;

            if(!tronWebState.loggedIn) {
                console.log('Logged in =', tronWebState.loggedIn, 'keep trying try=', tries);
                return tries++;
            } else {
                console.log('stop timer on try=', tries , ' tronWebState.installed=', tronWebState.installed, ' window.tronWeb=', window.tronWeb)
                clearInterval(timer)
                startBlockListener(window.tronWeb)
                startEventListener(window.tronWeb)
                resolve(window.tronWeb)
            }
        }, 100);
    }
})

export { GetTronWeb, GetBallance, mathdiv, timeout };