import Vue from 'vue'
import Vuex from 'vuex'
import state from './state'
import { GetTronWeb, GetBallance/* , GetAsset, blockToTime, LoanLastState  */} from './../util/getTronWeb.js'

Vue.use(Vuex)

export default new Vuex.Store({
  state: state,
  mutations: {
    registerTronWebInstance (state, payload) {
      console.log('committing mutation registerTronWebInstance', payload)
      state.tronWeb.instance = payload
      console.log('payload.ready=', payload.ready)
      state.tronWeb.connected = (payload.ready === true ? true : false)
      GetBallance()
    },
    mutationSetAddress (state, payload) {
      state.mainAddress = payload
      state.mainAddressShort = payload.substring(0, 5) + '....' + payload.substring(payload.length-5)
    },
    MutationSetTRXBal (state, payload) {
      state.trxBallance = payload
      state.trxBallanceNice = payload / 10**6
    },
    MutationSetBlockHistory(state, payload) {
      (state.blocksHistory.length == 0 ? state.blocksHistory.push(payload) : {});
      if (state.blocksHistory[state.blocksHistory.length-1].Number != payload.Number)
      {
        if (state.blocksHistory.length > 3) {state.blocksHistory.shift()}
        state.blocksHistory.push(payload);
      }
    },
    writeAllAwardsMutation(state, payload) {
      // add to state.awardHistory
      state.awardHistory.unshift({
      //state.awardHistory.push({
        '_betKeyStakeId': payload._betKeyStakeId,
        '_BlockNumber':   payload._BlockNumber,
        '_from':          payload._from,
        '_Prediction':    payload._Prediction,
        '_StakeId':       payload._StakeId,
        '_ActualTail':    payload._ActualTail,
        '_StakeValue':    payload._StakeValue,
        '_Won':           payload._Won,
        '_isWon':         payload._isWon
      })
      if (state.awardHistory.length > 100) {
        state.awardHistory.splice(101, 999999);
      }
    },
    writeStakeAwardMutation(state, payload) {
      state.awardHistory.forEach(element => {
        if (element._betKeyStakeId == payload._betKeyStakeId) {
          element._ActualTail = payload._ActualTail;
          element._Won = payload._Won;
          element._isWon = payload._isWon;
        }
      });
    },
    writeRiddleHashContractAddressMutation(state, payload) {
      state.riddleHashContractAddress = payload;
    },
    writeStakeMutation(state, payload) {
      state.gameStakeNice = payload;
      state.gameStake = payload * 10**6;
    },
  },
  actions: {
    registerTronWeb ({commit}) {
      console.log('start registerTronWebInstance')
      GetTronWeb.then(result => {
        console.log('registerTronWebInstance action result=', result, window.tronWeb)
        commit('registerTronWebInstance', result)
      }).catch(e => {
        console.log('registerTronWebInstance action error=', e)
      })
    },
    ActionSetAddress ({commit}, payload) {
      commit('mutationSetAddress', payload)
    },
    ActionSetTRXBal ({commit}, payload) {
      commit('MutationSetTRXBal', payload)
    },
    ActionSetBlockHistory ({commit}, payload) {
      commit('MutationSetBlockHistory', payload)
    },
    writeAllAwards({commit}, payload) {
      console.log('writeAllAwards started')
      commit('writeAllAwardsMutation', payload)
    },
    writeStakeAward({commit}, payload) {
      console.log('writeStakeAward started')
      commit('writeStakeAwardMutation', payload)
    },
    writeRiddleHashContractAddress({commit}, payload) {
      console.log('writeRiddleHashContractAddress started')
      commit('writeRiddleHashContractAddressMutation', payload)
    },
    writeStake({commit}, payload) {
      console.log('writeStake started')
      commit('writeStakeMutation', payload)
    }
  },
  modules: {
  }
})
